.carousel-inner {
  position: relative;
  overflow: hidden;
}

.carousel-item {
  display: block !important;
  visibility: hidden;
  height: 0;
  opacity: 0;
  -webkit-transition: opacity .6s ease-in, -webkit-transform 0ms ease-in-out;
  transition: opacity .6s ease-in, -webkit-transform 0ms ease-in-out;
  -o-transition: transform 0ms ease-in-out, opacity .6s ease-in;
  transition: transform 0ms ease-in-out, opacity .6s ease-in;
  transition: transform 0ms ease-in-out, opacity .6s ease-in, -webkit-transform 0ms ease-in-out;
  -webkit-transform: translate3d(0, 0, 0) !important;
          transform: translate3d(0, 0, 0) !important;
}

.carousel-item.active {
  opacity: 1;
  left: auto;
  height: auto;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0) !important;
          transform: translate3d(0, 0, 0) !important;
}

.carousel-multi-item .carousel-item {
  display: inline-block !important;
}

.carousel .carousel-slide-item {
  -webkit-transition: left 0.5s;
  -o-transition: left 0.5s;
  transition: left 0.5s;
}


